
ul {
  list-style: none;
}
html, body {
  height: 100%;
  -webkit-tap-highlight-color: transparent;
}
body {
  background-color: #F8F8F8;
  font-family: -apple-system-font, "Helvetica Neue", Helvetica, sans-serif;
}
.page__hd {
  padding: 20px;
}
.page__hd h1 {
    text-align: left;
    font-size: 20px;
    font-weight: 400;
}
.page__hd p {
    margin-top: 5px;
    color: #888;
    text-align: left;
    font-size: 14px;
}
.page__bd_spacing {
  padding: 0 15px;
}
.page__ft {
  padding-top: 40px;
  padding-bottom: 10px;
  text-align: center;
}
.page__ft.j_bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}
.weui-btn-area {
  margin: 1.3em 15px;
}
.weui-cell__ft_rg {
  margin-right: -5px;
  margin-bottom: -6px;
}
.weui-radio-group {
  display: inline-block;
  font-size: 0;
  line-height: 1;
}
.weui-radio-group .weui-radio-button {
    position: relative;
    overflow: hidden;
    display: inline-block;
}
.weui-radio-group .weui-radio-button input[type='radio'] {
      opacity: 0;
      outline: none;
      position: absolute;
      z-index: -1;
      left: -999px;
}
.weui-radio-group .weui-radio-button input[type='radio']:checked + span {
        color: #fff;
        background-color: #3cc51f;
        border-color: #3cc51f;
}
.weui-radio-group .weui-radio-button span {
      min-width: 4em;
      display: inline-block;
      line-height: 1;
      white-space: nowrap;
      vertical-align: middle;
      background: #fff;
      border: 1px solid #3cc51f;
      color: #3cc51f;
      -webkit-appearance: none;
      text-align: center;
      box-sizing: border-box;
      outline: none;
      margin: 0;
      position: relative;
      cursor: pointer;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      -webkit-user-select: none;
      padding: 5px 12px;
      font-size: 12px;
      border-radius: 0;
}
.weui-radio-group .weui-radio-button:first-child span {
      border-radius: 4px 0 0 4px;
}
.weui-radio-group .weui-radio-button:not(:last-child) {
      margin-right: -1px;
}
.weui-radio-group .weui-radio-button:last-child span {
      border-radius: 0 4px 4px 0;
}



